body {
    background: #ffffff;
}

#root_lightbox {
    background-color: rgba(200, 200, 200, 0.6);
}

.comments_section {
    margin: 0;
}

#widget_header {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background: #F5F5F5;

    #widget_logo {
        position: relative;
        height: 28px;
        width: 150px;
        margin-left: 20px;
        top: 16px;
    }

    #widget_user {
        display: table;
        position: absolute;
        top: 0;
        right: 20px;
        height: 60px;

        .profile_image_wrapper {
            width: 28px;

            #widget_user_image {
                width: 28px;
                height: 28px;
                margin-top: 16px;
            }
        }

        #user_name {
            height: 60px;
            color: #252525;
            font-family: "Open Sans", sans-serif;
            font-size: 14px;
            font-weight: bold;
            line-height: 19px;
            padding-right: 10px;
        }

        #login, #log_out {
            height: 19px;
            color: #0680FC;
            font-family: "Open Sans", sans-serif;
            font-size: 13px;
            line-height: 19px;
        }
    }
}
